/* Page center */
.otp-wrapper {
  min-height: 100vh;
  background: #f5f5f5;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
}

/* Card */
.otp-card {
  width: 360px;
  padding: 30px 24px;
  background: #fff;
  border-radius: 14px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
  text-align: center;
}

/* Title */
.otp-card h2 {
  margin-bottom: 6px;
  font-size: 22px;
  font-weight: 600;
}

/* Email */
.otp-email {
  font-size: 14px;
  color: #333;
  margin-bottom: 22px;
  word-break: break-all;
}

/* OTP boxes */
.otp-inputs {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-bottom: 24px;
}

.otp-inputs input {
  width: 52px;
  height: 52px;
  border-radius: 10px;
  border: 2px solid #ccc;
  font-size: 20px;
  text-align: center;
  outline: none;
}

.otp-inputs input:focus {
  border-color: #000;
}

/* Button */
.otp-card button {
  width: 140px;
  padding: 10px;
  border-radius: 20px;
  border: none;
  background: #000;
  color: #fff;
  font-size: 14px;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25);
}

.otp-card button:hover {
  opacity: 0.9;
}
