#toast-root {
  position: fixed;
  bottom: 24px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 9999;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Base toast */
.toast {
  min-width: 220px;
  max-width: 320px;
  padding: 12px 16px;
  border-radius: 8px;
  background: #000;
  color: #fff;
  font-size: 14px;
  text-align: center;
  opacity: 0;
  transform: translateY(10px);
  transition: all 0.3s ease;
  box-shadow: 0 6px 18px rgba(0,0,0,0.25);
}

/* Show animation */
.toast.show {
  opacity: 1;
  transform: translateY(0);
}

/* Types */
.toast-success {
  background: #000;
}

.toast-error {
  background: #b91c1c;
}

.toast-info {
  background: #111;
}
